@import url(https://fonts.googleapis.com/css?family=Homenaje|Mada:300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html {
  scroll-behavior: smooth;
  font-size: 20px;
  font-family: "Mada", sans-serif;
  box-sizing: border-box; }
  @media (max-width: 360px) {
    html {
      font-size: 12px; } }
  @media (min-width: 1600px) {
    html {
      font-size: 24px; } }
  html *,
  html *:before,
  html *:after {
    box-sizing: inherit; }

body {
  font-size: 20px;
  font-family: "Mada", sans-serif;
  font-weight: 400;
  line-height: 1.4;
  color: #fff;
  min-width: 360px; }

ul,
ol {
  padding: 0;
  margin: 0; }

small {
  font-size: 75%; }

a {
  color: inherit;
  text-decoration: none; }

img {
  max-width: 100%; }

h1,
.h1 {
  font-size: 58px;
  margin-bottom: 20px; }

h2,
.h2 {
  font-size: 50px;
  margin-bottom: 20px; }

h3,
.h3 {
  font-size: 30px;
  margin-bottom: 20px; }

p {
  font-size: 20px;
  margin: 20px 0; }

input {
  font-family: "Mada", sans-serif;
  border: none;
  background: none;
  padding: 0; }
  input::-webkit-input-placeholder {
    font-family: "Mada", sans-serif; }
  input::-moz-placeholder {
    font-family: "Mada", sans-serif; }
  input:-ms-input-placeholder {
    font-family: "Mada", sans-serif; }
  input::-ms-input-placeholder {
    font-family: "Mada", sans-serif; }
  input::placeholder {
    font-family: "Mada", sans-serif; }
  input:focus {
    box-shadow: 0 3px 5px #fff; }
  .is-submitted input:invalid {
    box-shadow: 0 3px 5px red; }
    .is-submitted input:invalid + .form-error-msg {
      visibility: visible; }

button {
  border: none;
  background: none;
  padding: 0; }

.btn:focus {
  outline: none; }

.wrapper {
  padding-left: 0;
  padding-right: 0;
  position: relative; }
  @media (min-width: 768px) {
    .wrapper {
      padding-left: 40px;
      padding-right: 40px; } }
  @media (min-width: 1024px) {
    .wrapper {
      max-width: 1024px;
      margin: 0 auto; } }
  @media (min-width: 1024px) {
    .wrapper {
      max-width: 1024px;
      margin: 0 auto; } }

.component {
  margin-top: 40px;
  margin-bottom: 40px; }

.hide-mob {
  display: none; }
  @media (min-width: 1024px) {
    .hide-mob {
      display: unset; } }

.actuallWeather {
  background-color: #809ee7;
  background-image: url(data:image/jpeg;base64,);
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  position: relative;
  left: 0;
  width: auto;
  margin: 0; }
  .actuallWeather::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: #809ee7;
    opacity: 0.7; }

.cityName {
  padding: 40px;
  text-align: center; }

.actual-cont {
  display: flex;
  flex-wrap: nowrap; }

.left-coll {
  position: relative;
  width: 50%;
  font-weight: 700;
  text-align: center; }

.right-coll {
  padding-left: 20px;
  text-align: center;
  width: 50%; }

.sunSet {
  display: flex;
  flex-wrap: nowrap;
  align-content: center; }

.sunIcon {
  padding: 5px;
  max-width: 40px;
  max-height: 40px; }

.arrow {
  padding: 0 5px; }

.sunTime {
  margin: 7px 0; }

.tempBoxCont {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.minMaxTemp {
  position: absolute;
  margin-top: -32px;
  margin-left: -60px;
  font-size: 14px; }
  .minMaxTemp img {
    padding: 0 5px; }

.weatherIcon,
.windSpeed,
.airPressure {
  margin: 20px 0 20px;
  width: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; }
  .weatherIcon img,
  .windSpeed img,
  .airPressure img {
    height: 40px;
