  .weatherIcon p,
  .windSpeed p,
  .airPressure p {
    margin: 0;
    padding: 5px 10px;
    white-space: nowrap; }

.fiveDay {
  color: black; }

.forcastList {
  display: flex;
  flex-wrap: wrap; }

.forecastItem {
  background-color: #adb7cf;
  width: 150px;
  padding: 10px;
  margin: 2px;
  border: 1px solid #809ee7;
  border-radius: 10px; }

.forecastItem {
  color: #000;
  text-align: center; }

.data {
  font-weight: 700;
  font-size: 10px; }

.tempBox--forecast p {
  font-weight: 700;
  font-size: 14px;
  margin-bottom: 0; }

.minMaxTemp--forecast {
  font-size: 14px; }
  .minMaxTemp--forecast img {
    padding: 0 5px; }

.weatherIcon--forecast,
.windSpeed--forecast,
.airPressure--forecast {
  font-size: 14px;
  display: inline-block; }
  .weatherIcon--forecast img,
  .windSpeed--forecast img,
  .airPressure--forecast img {
    width: 20px; }
  .weatherIcon--forecast span,
  .windSpeed--forecast span,
  .airPressure--forecast span {
